import React, { Component } from 'react';
import Header from './header';
import Main from './main';
import Footer from './footer';
class App extends Component {
    componentDidMount() {
        
       
        
    }
    render() {
        return (
            <div>
                <Header url={this.props.match.url}/>
                <div id="main">
                <Main url={this.props.match.url}/>
                </div>
                <Footer />
            </div>
        );
    }
}


export default App;


