import React, { Component } from 'react';
import page from "./page/index";
import HomePageDefault from "./page/HomePageDefault";
import logo from '../logo.svg';


class main extends Component {
  componentDidMount() {
console.log(this.props.url.slice(1));


  }
  render() {
    let pag = this.props.url.slice(1);
    if (/Article$/gmi.test(pag)) return <page.Article />;
    if (/Contacts$/gmi.test(pag)) return <page.Contacts />;
    if (/Cooperation$/gmi.test(pag)) return <page.Cooperation />;
    if (/DirectForm$/gmi.test(pag)) return <page.DirectForm />;
    if (/faq$/gmi.test(pag)) return <page.faq />;
    if (/faqAnswer$/gmi.test(pag)) return <page.faqAnswer />;
    if (/Fees$/gmi.test(pag)) return <page.Fees />;
    if (/HomePageDefault$/gmi.test(pag)) return <page.HomePageDefault />;
    if (/howThisWorks$/gmi.test(pag)) return <page.howThisWorks />;
    if (/htmltojsx$/gmi.test(pag)) return <page.htmltojsx />;
    if (/News$/gmi.test(pag)) return <page.News />;
    if (/Payment$/gmi.test(pag)) return <page.Payment />;
    if (/Reviews$/gmi.test(pag)) return <page.Reviews />;
    if (/SignIn$/gmi.test(pag)) return <page.SignIn />;
    if (/SignUp$/gmi.test(pag)) return <page.SignUp />;
    else return <page.HomePageDefault />;
  }
}

export default main;



