import React, { Component } from 'react';
import logo from '../../logo.svg';
class SignUp extends Component {
  componentDidMount() {


}
  render() {
    return (
      <main className="page-content sign-page">
      <div className="layout">
        <div className="breadcrumbs">
          <a href="javascript:void(0);">Главная</a> &gt;
          <span>Регистрация</span>
        </div>
        <div className="page-title">Регистрация</div>
        <div className="box">
          <form>
            <div className="rows">
              <div className="row">
                <label htmlFor="email">Введите ваш e-mail:</label><br />
                <input type="email" id="email" placeholder="Ваш email" />
              </div>
              <div className="row">
                <div className="one-row">
                  <label htmlFor="password">Придумайте пароль:</label>
                  <a href="javascript:void(0);" className="forgot-password">Требования к паролю</a>
                  <div className="clear" />
                </div>
                <input type="password" id="password" placeholder="Ваш пароль" />
              </div>
              <div className="row">
                <label htmlFor="password1">Повторите ввод пароля:</label><br />
                <input type="password" id="password1" placeholder="Еще раз пароль" />
              </div>
            </div>
            <div className="captcha">
              <div className="text">
                Антибот проверка:<br />
                Поставьте галочку
              </div>
              <div className="recaptcha">
                recaptcha
              </div>
              <div className="clear" />
            </div>
            <div className="agreement">
              <input type="checkbox" />
              Я&nbsp;согласен с&nbsp;<a href="javascript:void(0);">условиями и&nbsp;правилами сервиса</a>
            </div>
            <button type="submit" className="button">Зарегистрироваться</button>
            <div className="register">
              Уже есть аккаунт? <a href="javascript:void(0);">Войдите в Личный кабинет!</a>
            </div>
          </form>
        </div>
      </div>
    </main>
    );
  }
}

export default SignUp;



