import React, { Component } from 'react';
import logo from '../../logo.svg';
class SignIn extends Component {
  componentDidMount() {


}
  render() {
    return (
      <main className="page-content sign-page">
        <div className="layout">
          <div className="breadcrumbs">
            <a href="javascript:void(0);">Главная</a> &gt;
            <span>Войти в Личный кабинет</span>
          </div>
          <div className="page-title">Войти в Личный кабинет</div>
          <div className="box">
            <form>
              <div className="rows">
                <div className="row">
                  <label htmlFor="email">Введите ваш e-mail:</label><br />
                  <input type="email" id="email" placeholder="Ваш email" />
                </div>
                <div className="row">
                  <div className="one-row">
                    <label htmlFor="password">Введите пароль:</label>
                    <a href="javascript:void(0);" className="forgot-password">Забыли пароль?</a>
                    <div className="clear" />
                  </div>
                  <input type="password" id="password" placeholder="Ваш пароль" />
                </div>
              </div>
              <button type="submit" className="button">Войти в Личный кабинет</button>
              <div className="under-button">
                Переходя в&nbsp;личный кабинет, вы&nbsp;соглашаетесь с&nbsp;условиями и&nbsp;правилами сервиса.
              </div>
              <div className="register">
                Еще нет аккаунта? <a href="javascript:void(0);">Зарегистрируйтесь!</a>
              </div>
            </form>
          </div>
        </div>
      </main>
    );
  }
}

export default SignIn;



